"use client"

import { useState, useEffect } from "react"
import { useAuth } from "@/contexts/auth-context"
import { useRouter } from "next/navigation"
import ChatbotPanelWithHistory from "@/components/chatbot-panel-with-history"
import { UserMenu } from "@/components/user-menu"

const CHAT_STATE_KEY = 'chat_expanded_state'

export default function Home() {
  const [isChatExpanded, setIsChatExpanded] = useState(false) // Colapsado por padrão
  const { isAuthenticated, user } = useAuth()
  const router = useRouter()
  
  // Carregar estado do localStorage
  useEffect(() => {
    const savedState = localStorage.getItem(CHAT_STATE_KEY)
    if (savedState !== null) {
      setIsChatExpanded(savedState === 'true')
    }
  }, [])
  
  // Proteger rota
  useEffect(() => {
    if (!isAuthenticated) {
      router.push('/login')
    }
  }, [isAuthenticated, router])
  
  // Salvar estado no localStorage
  const handleToggleChat = () => {
    const newState = !isChatExpanded
    setIsChatExpanded(newState)
    localStorage.setItem(CHAT_STATE_KEY, String(newState))
  }

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-primary/30 border-t-primary rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-sm text-muted-foreground">Carregando...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header de exemplo */}
      <header className="fixed top-0 left-0 right-0 border-b border-border bg-background h-16 flex items-center justify-between px-6 z-50">
        <h1 className="text-xl font-semibold">Chat Template Demo</h1>
        <UserMenu />
      </header>

      {/* Área de conteúdo principal */}
      <main className="p-6 pt-24">
        <div className="max-w-4xl mx-auto space-y-6">
          <div className="bg-card border border-border rounded-lg p-6">
            <h2 className="text-2xl font-bold mb-4">Bem-vindo ao Chat Template</h2>
            <p className="text-muted-foreground mb-4">
              Este é um template de chat moderno e responsivo que você pode usar em qualquer aplicação React/Next.js.
            </p>
            
            <div className="space-y-2">
              <h3 className="font-semibold">Características:</h3>
              <ul className="list-disc list-inside space-y-1 text-sm text-muted-foreground">
                <li>Painel lateral colapsável e expansível</li>
                <li>Redimensionável arrastando a borda esquerda</li>
                <li>Suporte a Markdown nas mensagens</li>
                <li>Design moderno com Tailwind CSS</li>
                <li>Totalmente customizável</li>
              </ul>
            </div>
          </div>

          <div className="bg-card border border-border rounded-lg p-6">
            <h3 className="font-semibold mb-2">Como usar:</h3>
            <ol className="list-decimal list-inside space-y-2 text-sm text-muted-foreground">
              <li>Clique no botão de seta no canto superior direito para expandir/colapsar o chat</li>
              <li>Quando expandido, arraste a borda esquerda do painel para redimensionar</li>
              <li>Digite sua mensagem e pressione Enter ou clique no botão de enviar</li>
              <li>Use as sugestões rápidas para começar uma conversa</li>
            </ol>
          </div>

          <div className="bg-muted/50 border border-border rounded-lg p-6">
            <h3 className="font-semibold mb-2">Integração:</h3>
            <p className="text-sm text-muted-foreground mb-3">
              Para integrar este chat em sua aplicação, modifique o hook <code className="bg-muted px-1 rounded">use-chat.ts</code> para conectar com sua API.
            </p>
            <pre className="bg-card border border-border rounded p-3 text-xs overflow-x-auto">
{`import ChatbotPanel from '@/components/chatbot-panel'
import { useState } from 'react'

function App() {
  const [isExpanded, setIsExpanded] = useState(false)
  
  return (
    <ChatbotPanel 
      isExpanded={isExpanded} 
      onToggle={() => setIsExpanded(!isExpanded)} 
    />
  )
}`}
            </pre>
          </div>
        </div>
      </main>

      {/* Chat Panel with History */}
      <ChatbotPanelWithHistory isExpanded={isChatExpanded} onToggle={handleToggleChat} />
    </div>
  )
}
