import { useState, useCallback, useEffect } from 'react';
import { Message, EntityMention } from '@/types';
import { sendChatMessage, healthCheck } from '@/services/api';

interface UseChatOptions {
  initialMessages?: Message[];
  onError?: (error: Error) => void;
  onMessageSent?: (message: Message) => void;
  sessionId?: string;
}

export function useChat({ initialMessages = [], onError, onMessageSent, sessionId }: UseChatOptions = {}) {
  const [messages, setMessages] = useState<Message[]>(initialMessages);
  const [isLoading, setIsLoading] = useState(false);
  const [backendAvailable, setBackendAvailable] = useState(true);
  const [currentSessionId] = useState(sessionId || `session-${Date.now()}`);

  // Extrair menções de entidades da mensagem
  const extractMentions = (content: string): EntityMention[] => {
    const mentionRegex = /@\[([^\]]+)\]\(([^:]+):([^)]+)\)/g;
    const mentions: EntityMention[] = [];
    let match;
    
    while ((match = mentionRegex.exec(content)) !== null) {
      const [, name, type, id] = match;
      mentions.push({
        id,
        type: type as any,
        name,
      });
    }
    
    return mentions;
  };

  // Função para simular resposta do bot
  // SUBSTITUA ESTA FUNÇÃO pela integração com sua API
  const generateBotResponse = useCallback(async (userMessage: string, mentions?: EntityMention[]): Promise<{ answer: string; sources?: string[] }> => {
    // Simula delay de rede
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // Resposta simulada - SUBSTITUA pela chamada à sua API
    const responses = [
      "Entendo sua pergunta. Posso ajudá-lo com isso!",
      "Ótima pergunta! Deixe-me explicar...",
      "Vou processar sua solicitação e retornar com uma resposta detalhada.",
      "Isso é interessante! Aqui está o que posso te dizer...",
    ];
    
    const randomResponse = responses[Math.floor(Math.random() * responses.length)];
    
    let answer = `${randomResponse}\n\n**Sua pergunta foi:** "${userMessage}"`;
    
    // Se houver menções, adicionar informação sobre elas
    if (mentions && mentions.length > 0) {
      answer += `\n\n**Entidades mencionadas:**\n`;
      mentions.forEach(m => {
        answer += `- ${m.name} (${m.type})\n`;
      });
    }
    
    answer += `\n\n*Esta é uma resposta simulada. Integre com sua API em \`src/hooks/use-chat.ts\`*`;
    
    return {
      answer,
      sources: ['Sistema Local', 'Base de Conhecimento']
    };
  }, []);

  // Função para enviar mensagem
  const sendMessage = useCallback(async (content: string) => {
    if (!content.trim()) return;

    // Extrair menções da mensagem
    const mentions = extractMentions(content);

    const userMessage: Message = {
      id: Date.now().toString(),
      type: 'user',
      content: content.trim(),
      timestamp: new Date(),
      mentions: mentions.length > 0 ? mentions : undefined,
    };

    setMessages(prev => [...prev, userMessage]);
    
    // Callback para notificar que a mensagem foi enviada
    if (onMessageSent) {
      onMessageSent(userMessage);
    }
    
    setIsLoading(true);

    try {
      // SUBSTITUA esta chamada pela integração com sua API
      // Passe as menções para sua API poder usar contexto das entidades
      const response = await generateBotResponse(content, mentions);

      const botMessage: Message = {
        id: (Date.now() + 1).toString(),
        type: 'bot',
        content: response.answer,
        timestamp: new Date(),
        sources: response.sources,
      };

      setMessages(prev => [...prev, botMessage]);
      
      // Callback para notificar que a resposta foi recebida
      if (onMessageSent) {
        onMessageSent(botMessage);
      }
    } catch (error) {
      console.error('Erro ao processar mensagem:', error);
      
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        type: 'bot',
        content: 'Desculpe, ocorreu um erro ao processar sua pergunta. Tente novamente.',
        timestamp: new Date(),
      };
      
      setMessages(prev => [...prev, errorMessage]);
      
      if (onError && error instanceof Error) {
        onError(error);
      }
    } finally {
      setIsLoading(false);
    }
  }, [generateBotResponse, onError, onMessageSent]);

  // Função para limpar mensagens
  const resetMessages = useCallback((welcomeMessage?: string) => {
    const initialMessage: Message = {
      id: Date.now().toString(),
      type: "bot",
      content: welcomeMessage || 'Olá! Sou seu assistente virtual. Como posso ajudá-lo hoje?',
      timestamp: new Date(),
    };
    
    setMessages([initialMessage]);
  }, []);

  return {
    messages,
    isLoading,
    sendMessage,
    resetMessages,
  };
}
